.promo-list {
  position: relative;
  margin: 0;
  padding-left: 0;
  list-style: none;
  width: 100%;
}

.promo-list__link {
  position: relative;
  display: block;
  padding: 26px 30px 29px;
  font-size: 19px;
  font-weight: 700;
  color: $white;
  text-decoration: none;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background-repeat: no-repeat;
    opacity: 0.5;
  }

  &:hover {
    transform: translateY(-5px);

    &::before {
      bottom: -5px;
      height: 5px;
    }

    &::after {
      opacity: 0.7;
    }
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);

    &::after {
      opacity: 0.3;
    }
  }

  &--interior {
    background-color: $light-green;

    &:hover,
    &:active {
      background-color: $light-green;
    }

    &::before {
      background-color: $light-green;
    }

    &::after {
      right: 36px;
      width: 40px;
      height: 62px;
      background-image: url("../img/icon-interior.svg");
      background-size: 40px 62px;
    }
  }

  &--toy {
    background-color: $dark-green;

    &:hover,
    &:active {
      background-color: #46c1ae;
    }

    &::before {
      background-color: $dark-green;
    }

    &::after {
      right: 27px;
      width: 60px;
      height: 60px;
      background-image: url("../img/icon-toy.svg");
      background-size: 60px 60px;
    }
  }
}

.promo-list__text {
  display: block;
  max-width: 120px;
}

@media (min-width: $tablet) {
  .promo-list {
    display: flex;
    flex-wrap: wrap;
  }

  .promo-list__item {
    width: 50%;
  }

  .promo-list__link {
    padding: 28px 35px 31px;
    font-size: 20px;
    line-height: 30px;

    &--toy {
      &::after {
        right: 34px;
      }
    }
  }
}
